@import "../../theme.scss";
.Albumdetails {
  height: 100%;
  display: flex;
  flex-direction: column;
  .Albumdetails-header {
    height: 115px;
    padding: 10px 15px;
    background: rgba(255, 255, 255, 1);
    border-radius: 3px;
    border: 1px solid rgba(235, 235, 235, 1);
    .a-details-information {
      display: flex;
      .upphoto-btn {
        width: 120px;
        height: 48px;
        position: relative;
        display: inline-block;
        background: $bg-color;
        border-radius: 3px;
        overflow: hidden;
        color: white;
        text-indent: 0;
        text-align: center;
        align-self: center;
        line-height: 48px;
        margin-right: 15px;
        font-size: 14px;

        font-weight: 500;
        input {
          position: absolute;
          font-size: 100px;
          right: 0;
          top: 0;
          opacity: 0;
        }
      }
      .Album-file-num,
      .Album-creation-time,
      .Album-last-update {
        margin: 0 20px;
        .a-d-i-icon {
          height: 100%;
          display: inline-block;
          font-size: 32px;
          vertical-align: middle;
          margin-right: 10px;
          padding-top: 4px;
          i {
            font-size: 32px;
          }
          .creation-time-icon {
            display: block;
            width: 40px;
            height: 40px;
            background: url('../../images/icon2.png');
          }
  
          .last-update-icon {
            display: block;
            width: 40px;
            height: 40px;
            background: url('../../images/icon3.png');
          }
        }
        

        .Album-num-text {
          float: right;
          height: 100%;
          padding: 0 3px;
          text-align: left;
          .text {
            margin: 0;
            height: 24px;
            font-size: 13px;

            font-weight: 400;
            color: rgba(1, 1, 1, 0.6);
            line-height: 24px;
          }
          .num {
            margin: 0;
            height: 24px;
            font-size: 16px;

            font-weight: 400;
            color: rgba(25, 25, 25, 1);
            line-height: 24px;
          }
        }
      }
      .Album-info-btn {
        margin-left: auto;
        width: 49px;
        height: 49px;
        background: $bg-color;
        border-radius: 4px;
        text-align: center;
        cursor: pointer;
        i {
          color: #fff;
          font-size: 24px;
          padding-top: 2px;
          line-height: 49px;
        }
      }
    }
  }
  .Album-operation {
    display: flex;
    height: 45px;
    background: rgba(255, 255, 255, 1);
    border-radius: 3px;
    border: 1px solid rgba(235, 235, 235, 1);
    padding: 0 15px;
    .checkAll {
      align-self: center;
      display: flex;
      .a-check-frame {
        margin-right: 10px;
        font-size: 20px;
        cursor: pointer;
      }
      .icon-xuanzhong1{
        color: $bg-color;
      }
      .a-checkAll-text,
      .invert-Selection {
        font-size: 14px;
        color: rgba(2, 2, 2, 1);
        line-height: 20px;
        margin-right: 24px;
      }
      .invert-Selection {
        cursor: pointer;
        color: $bg-color;
      }
    }
    .Album-delete {
      align-self: center;
      padding-left: 24px;
      border-left: 1px solid #e9e9e9;
      span {
        display: block;
        height: 30px;
        padding: 0 10px;
        border-radius: 3px;
        border: 1px solid rgba(191, 191, 191, 1);
        font-size: 12px;
        font-weight: 400;
        color: rgba(31, 31, 31, 1);
        line-height: 30px;
        cursor: pointer;
        i {
          margin-right: 10px;
        }
      }
    }
  }

  .Albumdetails-main {
    flex: 1;
    overflow: auto;
  }

  .image-dialog {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.85);
    padding: 0 50px;
    z-index: 101;
    .image-dialog-boby {
      height: 100%;
      width: 100%;
      position: relative;
      .image-wrap {
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          max-height: 600px;
          max-width: 900px;
        }
        i {
          color: #09aaff;
        }
        .p_video {
          background-color: rgba(0, 0, 0, 0.85);
        }
      }
      .onPiece {
        height: 500px;
        width: 150px;
        position: absolute;
        left: 50px;
        top: 50%;
        transform: translateY(-50%);
        display: flex;
        align-items: center;
        cursor: pointer;
        .pre-arrow {
          height: 56px;
          width: 56px;
          border-radius: 50%;
          background-color: rgba(59, 59, 59, 0.6);
          font-size: 26px;
          color: #fff;
          text-align: center;
          line-height: 56px;
        }
      }
      .nextPage {
        height: 500px;
        width: 150px;
        position: absolute;
        right: 50px;
        top: 50%;
        transform: translateY(-50%);
        display: flex;
        align-items: center;
        text-align: right;
        cursor: pointer;
        .next-arrow {
          height: 56px;
          width: 56px;
          border-radius: 50%;
          background-color: rgba(59, 59, 59, 0.6);
          font-size: 26px;
          color: #fff;
          text-align: center;
          line-height: 56px;
          right: 0;
          position: absolute;
        }
      }
      .onPiece:hover .pre-arrow,
      .nextPage:hover .next-arrow {
        background-color: #09aaff;
      }
    }
    .close-image-dialog {
      position: fixed;
      right: 22px;
      top: 10px;
      color: #fff;
      font-size: 26px;
      cursor: pointer;
    }
  }
}

